
import React from 'react'
import { connect } from 'react-redux'

import Msg from '@components/Msg'
import { download } from '@utils/fetch'

class Upload extends React.Component {
  constructor(props) {
    super(props)
  }
  downloadHandler = () => {
    download(this.props.url,this.props.fileName)
  }

  render() {
    return (
      <React.Fragment>
        <Msg msg='批量修改会覆盖原有配置信息，请谨慎操作。'></Msg>
        <ul className="form-list">
          <li className="pure-text-row">
            <div className="form-input">
              <p className="form-text">第一步：下载已有产品信息模板文件 <a href="javascript:;" onClick={() => { this.downloadHandler() }} >点击下载文件</a></p>
            </div>
          </li>
          <li className="pure-text-row">
            <div className="form-input">
              <p className="form-text">第二步：编辑文件</p>
            </div>
          </li>
          <li className="pure-text-row">
            <div className="form-input">
              <p className="form-text">第三步：上传文件，覆盖原有信息</p>
            </div>
          </li>
          <li>
            <div className="form-input">
              <div className="form-unit form-upload is-success">
                <input type="text" className="tc-15-input-text m" readOnly placeholder={this.props.file ? this.props.file.name : '请选择文件'} />
                <a href="javascript:void(0);" className="tc-upload-btn" title="请选择文件">
                  <div className="file-input-wrap">
                    <input type="file" title="文件限制提示内容"
                      // accept={accept || 'aplication/zip'}
                      onChange={this.props.onAttachmentChange}
                    />
                    <span>选择文件</span>
                  </div>
                </a>
                <p className="form-input-help">
                  请选择小于20M的文件
                  {/* {this.props.isOverSize ? '请选择小于20M的文件' : ''} */}
                </p>
              </div>
            </div>
          </li>
        </ul>
      </React.Fragment>
    )
  }
}


export default connect()(Upload)
