
import React from 'react'
import { connect } from 'react-redux'
import { DataTable } from '@tencent/bere'
import "./style.css"
class DefinitionTable extends React.Component {
  render() {
    const {
      conditionTableData,
      updateTableData,
      loading,
      onSettingBtnClick
    } = this.props;
    const conditionTableColum = [{
      key: 'rule_field',
      title: '查询字段',
      render: (value, record) => {
        return (
          <div>
            <div><span class="text-overflow" title="=">{value.toUpperCase()}</span></div>
          </div>
        )
      }
    }, {
      key: 'op_type',
      title: '运算规则',
    }, {
      key: 'desc',
      title: '备注说明',
      render: (value, record) => {
        return (
          <div>
            <div><span class="text-overflow" title="=">{value.toUpperCase()}</span></div>
          </div>
        )
      }
    }, {
      key: 'setting',
      title: '操作',
      headerRender: () => {
        // return <td><div><span>操作</span></div></td>
        return <span style={{ fontSize: '12px', color: '#444', lineHeight: '18px' }}>操作</span>
      },
      render: (value, record) => {
        return (
          // <div>
          //   <a href="javascript:;"  onClick={() => { onSettingBtnClick('condition', 'edit', record) }}>修改</a>
          //   <a href="javascript:;" onClick={() => { onSettingBtnClick('condition', 'delete', record) }}>删除</a>
          // </div>
          <div>
            <a href="javascript:;" className="disable">修改</a>
            <a href="javascript:;" className="disable">删除</a>
          </div>
        )
      }
    }
    ];
    const updateTableColum = [{
      key: 'rule_field',
      title: '更新字段',
      render: (value, record) => {
        return (
          <div>
            <div><span class="text-overflow" title="=">{value.toUpperCase()}</span></div>
          </div>
        )
      }
    }, {
      key: 'desc',
      title: '异常单类型',
      render: (value, record) => {
        return (
          <div>
            <div><span class="text-overflow" title="=">{value.toUpperCase()}</span></div>
          </div>
        )
      }
    }, {
      key: 'setting',
      title: '操作',
      headerRender: () => {
        // return <td><div><span>操作</span></div></td>
        return <span style={{ fontSize: '12px', color: '#444', lineHeight: '18px' }}>操作</span>
      },
      render: (value, record) => {
        return (
          // <div>
          //   <a href="javascript:;" onClick={() => { onSettingBtnClick('update', 'edit', record) }}>修改</a>
          //   <a href="javascript:;" onClick={() => { onSettingBtnClick('update', 'delete', record) }}>删除</a>
          // </div>
          <div>
            <a href="javascript:;" className="disable">修改</a>
            <a href="javascript:;" className="disable">删除</a>
          </div>
        )
      }
    }]

    return (
      <React.Fragment>
        <div className="param-box definal-table">
          <div className="param-hd">
            <h3>条件项</h3>
          </div>
          <div className="param-bd big-header">
            <DataTable
              columns={conditionTableColum}
              data={conditionTableData}
              rowKey="id"
              loading={loading}
            />
          </div>
        </div>
        <div className="param-box definal-table">
          <div className="param-hd">
            <h3>更新项</h3>
          </div>
          <div className="param-bd">
            <DataTable
              columns={updateTableColum}
              data={updateTableData}
              rowKey="id"
              loading={loading}
            />
          </div>
        </div>
      </React.Fragment >
    )
  }
}

export default connect(null)(DefinitionTable)
