
import React from 'react'
import { connect } from 'react-redux'
import { DataTable, Pager, } from '@tencent/bere'

import "./style.css"
class InputTable extends React.Component {
  ruleTableColumAdapter = () => {
    let ruleTableColum = Object.assign([], this.props.ruleTableColum)
    ruleTableColum.forEach(col => {
      if (col.ruleType === 'update') {
        col.render = (value, record) => {
          return (
            <div><span className="text-overflow text-success" title={value}>{value}</span></div>
          )
        }
      }
      col.headerRender = (value, record) => {
        return <span className="text-overflow" title={value.original.desc.toUpperCase()}>{value.original.desc.toUpperCase()}</span>
      }
    });
    ruleTableColum.push({
      key: 'setting',
      title: '操作',
      headerRender: () => {
        // return <td><div><span>操作</span></div></td>
        return <span style={{ fontSize: '12px', color: '#444', lineHeight: '18px' }}>操作</span>
      },
      render: (value, record) => {
        return (
          <div>
            <a href="javascript:;" onClick={() => { this.props.handleSettingBtnClick('edit', record) }}>修改</a>
            <a href="javascript:;" onClick={() => { this.props.handleSettingBtnClick('delete', record) }}>删除</a>
          </div>
        )
      }
    })
    ruleTableColum.splice(0,0,{
      key: 'relate_id',
      dataIndex:'relate_id',
      title: '关联ID'
    })
    return ruleTableColum
  }
  render() {
    const {
      ruleTableData,
      ruleTablePageTotal,
      ruleTablePageCurrent,
      ruleTablePageCount,
      onPageChange,
      onPerPageChange,
      loading
    } = this.props
    let ruleTableColum = this.ruleTableColumAdapter()
    const pageState = {
      total: ruleTablePageTotal,
      current: ruleTablePageCurrent - 1,
      perPageCount: ruleTablePageCount,
      perPageCountList: [15, 30, 50],
      totalLimit: null,
      additionalMessage: null,
    };
    return (
      <React.Fragment>
        <div className="param-box">
          <div className="param-bd big-header">
            <DataTable
              columns={ruleTableColum}
              data={ruleTableData}
              rowKey="id"
              loading={loading}
            />
          </div>
        </div>
        <Pager
          total={pageState.total}
          selected={pageState.selected}
          current={pageState.current}
          perPageCountList={pageState.perPageCountList}
          perPageCount={pageState.perPageCount}
          onPageChange={page=>onPageChange(page+1)}
          onPerPageChange={onPerPageChange}
        />
      </React.Fragment>
    )
  }
}


export default connect()(InputTable)
