
import React from 'react'
import { connect } from 'react-redux'

class Frocess extends React.Component {
  render() {
    return (
      <React.Fragment>
        <div className="tc-15-progress">
          <div className="tc-15-progress-value" aria-valuemax="100" aria-valuemin="0" aria-valuenow="30" style={{ width: `${this.props.progress}%` }}>
            <span aria-valuetext="运行中">正在上传，请耐心等待</span>
          </div>
        </div>
      </React.Fragment>
    )
  }
}
export default connect()(Frocess)
