import React from 'react';
import ReactDOM from 'react-dom';
import { Dialog, Button } from '@tencent/bere'

export default class DeleteDialog extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      loading: false
    }
  }
  startLoading = () => {
    this.setState({
      loading: true
    })
  }
  endLoading = () => {
    this.setState({
      loading: false
    })
  }

  confirm = () => {
    let { confirmCallBack } = this.props;
    confirmCallBack(this.startLoading,this.endLoading);
  }
  cancel = () => {
    let { cancelCallBack } = this.props;
    cancelCallBack();
  }
  handleFooterRender = () => {
    return (
      <div className="tc-15-rich-dialog-ft-btn-wrap">
        <Button loading={this.state.loading} onClick={this.confirm}>确定</Button>
        <Button className="tc-15-btn weak" onClick={this.cancel} >取消</Button>
      </div>
    );
  }
  render() {
    return (
      <React.Fragment>
        <Dialog
          title=""
          visible={true}
          footerRender={this.handleFooterRender}
          onCancel={this.cancel}
          onOk={this.confirm}
        >
          <div className="tc-icon-box">
            <div className="col">
              <i className=""></i>
            </div>
            <div className="col">
              <h3 className="tc-dialog-title">确定删除所选站点产品？</h3>
              <p>删除后无法恢复</p></div>
          </div>
        </Dialog>
      </React.Fragment >
    )
  }
}



DeleteDialog.newInstance = (properties) => {
  let props = properties || {};
  let div = document.createElement('div');
  document.body.appendChild(div);
  ReactDOM.render(React.createElement(DeleteDialog, props), div);
  return {
    destroy() {
      ReactDOM.unmountComponentAtNode(div);
      document.body.removeChild(div);
    },
  };
};
