import React from 'react'
import { connect } from 'react-redux'
import RuleEdit from '@components/RuleEdit'

class RulePage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      loading: false
    }
  }
  onCancel = () => {
    this.props.dispatch({
      type: 'glass/change',
      payload: {
        addRulePageShow: false
      }
    })
  }
  handleAddRuleOk = (data) => {
    this.setState({
      loading: true
    })
    let callback = (error) => {
      this.setState({
        loading: false
      })
      if (error) {
        return
      }
      this.onCancel()
    }
    if (this.props.addRulePageIsEdit) {
      this.props.dispatch({
        type: "glass/editRule",
        payload: {
          data: data,
          id: this.props.editColumData.id,
        },
        callback
      })
    } else {
      this.props.dispatch({
        type: "glass/addRule",
        payload: ({
          data: data,
        }),
        callback
      })
    }
  }
  handleNavSelectChange = (val) => {
    this.props.dispatch({
      type: 'glass/change',
      payload: {
        navSelect: val
      }
    })
  }
  render() {
    return (
      <React.Fragment>
        <RuleEdit
         loading={this.state.loading}
          onCancel={this.onCancel}
          onOk={this.handleAddRuleOk}
          ruleColum={this.props.addRulePageColum}
          isEdit={this.props.addRulePageIsEdit}
          editColumData={this.props.editColumData}
        />
      </React.Fragment>
    )
  }
}

const mapStateToProps = ({ glass }) => {
  return {
    addRulePageColum: glass.addRulePageColum,
    addRulePageIsEdit: glass.addRulePageIsEdit,
    editColumData: glass.editColumData,
  }
}

export default connect(mapStateToProps)(RulePage)
