
import React from 'react'
import { Button } from '@tencent/bere'

class BtnGroup extends React.Component {
  render() {
    const { creatFncHandle, editFncHandle } = this.props
    return (
      <div className="tc-action-grid mb-10">
        <div className="justify-grid">
          <div className="col">
            <Button className="tc-15-btn m btn-add" disabled={this.props.disabled} onClick={() => { creatFncHandle() }}>新建</Button>
            <Button className="tc-15-btn m weak" disabled={this.props.disabled} onClick={() => { editFncHandle() }}>批量修改</Button>
          </div>
          <div className="col"></div>
        </div>
      </div>
    )
  }
}

export default BtnGroup
