
import React from 'react'
import { connect } from 'react-redux'
import { NavLink } from 'react-router-dom'
import { notification } from '@tencent/bere'

import UploadDialog from '@components/Upload'
import CreatDialog from '@components/CreatDialog'
import BtnGroup from '@components/BtnGroup'
import Msg from '@components/Msg'
import DefineTable from '@components/DefineTable'

import Panel from '@layouts/PanelLayout.js'
import DeleteDialog from '@components/DeleteDialog'

class RuleDefinition extends React.Component {
  constructor(props) {
    super(props)
    this.state = ({
      uplodVisable: false
    })
  }
  componentDidMount() {
    this.fetchTable()
  }
  //获取数据
  fetchTable = () => {
    this.props.dispatch({
      type: 'glass/fetchRuleDefinalTable'
    })
  }
  //新建
  handleCreatBtn = () => {
    this.props.dispatch({
      type: 'glass/change',
      payload: {
        creatModalShow: {
          visible: true,
          isEdit: false,
          initialValues: {
            ruleType: 'condition'
          }
        }
      }
    })
  }
  //批量编辑
  handleBulkEdit = () => {
    this.setState({ uplodVisable: true })
  }
  //表格编辑按钮
  handleSettingBtnClick = (type, setting, record) => {
    if (setting === "edit") {
      this.props.dispatch({
        type: 'glass/change',
        payload: {
          creatModalShow: {
            visible: true,
            isEdit: true,
            initialValues: {
              ruleType: type,
              field: record.field,
              op_type: record.op_type,
              name: record.name,
              desc: record.desc
            }
          }
        }
      })
    } else {
      DeleteDialog.open({
        confirmCallBack(startLoading, endLoading) {
          startLoading()
          setTimeout(() => {
            endLoading()
            DeleteDialog.close()
          }, 1000)
        },
        cancelCallBack() {
          DeleteDialog.close()
        }
      })
    }
  }
  //新建modal ok
  handleCreatDialoglOk = (data) => {
    if (data.type === 'condition') {
      if (!data.type || !data.desc || !data.op_type) {
        notification.error('请检查输入!', 2000)
      }
    }
    this.handleCreatDialoglCancel()
  }
  //关闭新建modal
  handleCreatDialoglCancel = () => {
    this.props.dispatch({
      type: 'glass/change',
      payload: {
        creatModalShow: {
          visible: false,
          isEdit: false,
          initialValues: {
            ruleType: 'condition'
          }
        }
      }
    })
  }

  render() {
    const { conditionTableData, updateTableData, defineTableLoading } = this.props;
    return (
      <React.Fragment>
        <Msg msg='设置完成后请在配置生效界面进行生效操作 '>
          <NavLink exact to="/suggest/apply" activeStyle={{ color: '#4dc060' }}>前往配置生效页面 </NavLink>
        </Msg>
        <BtnGroup
          disabled={true}
          creatFncHandle={() => this.handleCreatBtn('creatModal')}
          editFncHandle={() => this.handleBulkEdit('uploadModal')}
        />
        <Panel>
          <DefineTable
            conditionTableData={conditionTableData}
            updateTableData={updateTableData}
            loading={defineTableLoading}
            onSettingBtnClick={this.handleSettingBtnClick}
          />
        </Panel>
        <CreatDialog
          visible={this.props.visible}
          isEdit={this.props.isEdit}
          initialValues={this.props.initialValues}
          onOk={this.handleCreatDialoglOk}
          onCancel={this.handleCreatDialoglCancel}
        />
        <UploadDialog
          visible={this.state.uplodVisable}
          onCancel={() => {
            this.setState({
              uplodVisable: false
            })
            this.fetchTable()
          }}
        />
      </React.Fragment >
    )
  }
}

const mapStateToProps = ({ glass }) => {
  const { creatModalShow: { visible, isEdit, initialValues } } = glass;

  return {
    defineTableLoading: glass.definitionTableLoading,
    conditionTableData: glass.conditionTableData,
    updateTableData: glass.updateTableData,

    visible: visible,
    isEdit: isEdit,
    initialValues: {
      type: initialValues.ruleType || 'condition',
      field: initialValues.field || '',
      op_type: initialValues.op_type || '=',
      name: initialValues.name || '',
      desc: initialValues.desc || ''
    }
  }
}

export default connect(mapStateToProps)(RuleDefinition)
