
import React from 'react'
import { connect } from 'react-redux'
import { Input, Dialog, FormGroupList, StaticFormGroup, RadioButtonGroup, Select } from '@tencent/bere'
import {
  FormGroupField
} from '@tencent/bere-redux-form'
import { reduxForm, formValueSelector } from 'redux-form'

export const FORM_NAME = 'creatDialogForm'

class CreatDialog extends React.Component {
  render() {
    const typeList = [
      {
        text: '条件项',
        value: 'condition'
      },
      {
        text: '更新项',
        value: 'update'
      },
    ];
    const opList = [
      {
        text: '=',
        value: '='
      },
      {
        text: '+',
        value: '+'
      },
      {
        text: '-',
        value: '-'
      },
      {
        text: '*',
        value: '*'
      },
      {
        text: '/',
        value: '/'
      },
    ]
    return (
      <Dialog
        title="新建规则"
        visible={true}
        okText="确认"
        cancelText="取消"
        onOk={this.props.handleSubmit(this.props.onOk)}
        onCancel={this.props.onCancel}
      >
        <FormGroupList>
          {
            this.props.isEdit ?
              <StaticFormGroup label="表单域">
                {this.props.ruleType === 'condition' ? '条件项' : '更新项'}
              </StaticFormGroup> :
              <FormGroupField
                name='type'
                label='定义类型'
                component={RadioButtonGroup}
                list={typeList}
              />
          }
          {
            this.props.ruleType === 'condition' ?
              <React.Fragment>
                <FormGroupField
                  name='field'
                  label='查询字段'
                  component={Input}
                />
                <FormGroupField
                  name='desc'
                  label='备注说明'
                  component={Input}
                />
                <FormGroupField
                  name='op_type'
                  label='运算规则'
                  component={Select}
                  list={opList}
                />
              </React.Fragment> :
              <React.Fragment>
                <FormGroupField
                  name='field'
                  label='更新字段'
                  component={Input}
                />
                <FormGroupField
                  name='desc'
                  label='异常单类型'
                  component={Input}
                />
              </React.Fragment>
          }
        </FormGroupList>
      </Dialog>
    )
  }
}

const formSelector = formValueSelector(FORM_NAME)
const FormDialog = reduxForm({
  form: FORM_NAME,
})(CreatDialog)

function mapStateToProps(state) {
  const ruleType = formSelector(state, 'type')
  return {
    ruleType
  }
}
export default connect(mapStateToProps)(props => (props.visible ? <FormDialog {...props} /> : null))
