
import React from 'react'
import { connect } from 'react-redux'
import { Dialog } from '@tencent/bere'
import { Button, notification } from "@tencent/bere"

import { suggestServer } from '@config/urlMap'

import Upload from './Upload'
import Finish from './Finish'
import Process from './Process'

let ajax = null;//ajax对象

class UploadDialog extends React.Component {
  constructor(props) {
    super(props)
    let url,
      fileName;
    switch (this.props.type) {
      case 1:
        url = `${suggestServer}/file/defect_code.csv?site=${this.props.site}&&shop=${this.props.shop}&&version=${this.props.version}`
        fileName = 'defect_code.csv'
        break;
      case 2:
        url = `${suggestServer}/file/bug.csv?site=${this.props.site}&&shop=${this.props.shop}&&version=${this.props.version}`
        fileName = 'bug.csv'
        break;
      case 3:
        url = `${suggestServer}/file/bug_filter.csv?site=${this.props.site}&&shop=${this.props.shop}&&version=${this.props.version}`
        fileName = 'bug_filter.csv'
        break;
      case 4:
        url = `${suggestServer}/file/glass.csv?site=${this.props.site}&&shop=${this.props.shop}&&version=${this.props.version}`
        fileName = 'glass.csv'
        break;
      default:
        break;
    }
    this.state = {
      tab: 1,//1.选择文件 2.上传 3.确认
      file: null,
      progress: 0,
      fileName,
      url: url
    }
  }
  componentDidMount() {
  }
  handleUplodOk = () => {
    if (!this.state.file) {
      notification.warning('请选择文件')
      return;
    } else if (this.state.file.size >= 20 * 1024 * 1024) {
      notification.warning('请选择小于20m的文件哦')
      return
    } else {
      this.setState({
        tab: 2,
        progress: 0
      }, () => {
        this.startUpload()
      })
    }
  }
  startUpload() {
    let that = this;
    var formData = new FormData();

    formData.append("file", this.state.file, this.state.fileName);
    ajax = $.ajax({
      url: `${suggestServer}/rule/upload?site=${this.props.site}&&shop=${this.props.shop}&&version=${this.props.version}`,
      type: "POST",
      data: formData,
      processData: false, // 不要对data参数进行序列化处理，默认为true
      contentType: false, // 不要设置Content-Type请求头，因为文件数据是以 multipart/form-data 来编码
      beforeSend: function (xhr) {
        xhr.setRequestHeader("Authorization", `Bearer ${localStorage.getItem("accessToken")}`)
      },
      xhr: function () {
        let myXhr = $.ajaxSettings.xhr();
        if (myXhr.upload) {
          myXhr.upload.addEventListener('progress', function (e) {
            if (e.lengthComputable) {
              var percent = Math.floor(e.loaded / e.total * 100);
              console.log(percent)
              that.setState({
                progress: percent
              })
            }
          }, false);
        }
        return myXhr;
      },
      success: (res) => {
        if(typeof(res) =="string"){
          res = JSON.parse(res)
        }
        if (res.code !== 0) {
          notification.error(res.msg)
          // switch (res.code) {
          //   case 10000:
          //     notification.error('服务器错误')
          //     break;
          //   case 10001:
          //     notification.error('参数错误')
          //     break;
          //   case 10002:
          //     notification.error('上传文件格式非法')
          //     break;
          //   case 10003:
          //     notification.error('上传文件名非法')
          //     break;
          //   default:
          //   notification.error('上传文件出错')
          //     break;
          // }
          this.handleUplodChance()
          return
        }
        that.setState({
          progress: 100,
          tab: 3
        }, () => {
          ajax = null
        })
      },
      error: (err) => {
        this.handleUplodChance()
        notification.error("上传失败")
      }
    });

  }
  handleUplodChance = () => {
    if (ajax) {
      ajax.abort()
      ajax = null
    }
    this.setState({
      tab: 1,
    })

  }
  onAttachmentChange = (e) => {
    e.preventDefault()
    let file = e.target.files[0]
    if (file && file.size >= 20 * 1024 * 1024) {
      notification.warning('请选择小于20m的文件哦')
      file = null
    }
    this.setState({
      file: file || null,
    })
  }
  handleModalCancel = () => {
    this.props.onCancel()
  }

  render() {
    let content;
    if (this.state.tab === 1) {
      content = <Upload
        {...this.state}
        fileName = {this.state.fileName}
        url={this.state.url}
        onAttachmentChange={this.onAttachmentChange}
      />;
    } else if (this.state.tab === 2) {
      content = <Process
        {...this.state}
      />;
    } else if (this.state.tab === 3) {
      content = <Finish />;
    }
    let handleFooterRender = () => {
      switch (this.state.tab) {
        case 1:
          return (
            <div className="tc-15-rich-dialog-ft">
              <div className="tc-15-rich-dialog-ft-btn-wrap">
                <Button onClick={() => { this.handleUplodOk() }}>确认</Button>
                <Button onClick={() => { this.handleModalCancel() }}>取消</Button>
              </div>
            </div>
          )
          break;
        case 2:
          return (
            <div className="tc-15-rich-dialog-ft">
              <div className="tc-15-rich-dialog-ft-btn-wrap">
                <Button onClick={() => { this.handleUplodChance() }}>取消</Button>
              </div>
            </div>
          )
          break;
        case 3:
          return (
            <div className="tc-15-rich-dialog-ft">
              <div className="tc-15-rich-dialog-ft-btn-wrap">
                <Button onClick={() => { this.handleModalCancel() }}>我知道了</Button>
              </div>
            </div>
          )
          break;
        default:
          return ''
          break
      }
    }
    return (
      <Dialog
        title={this.tab === 3 ? '批量修改成功' : '批量修改'}
        visible={this.props.visible}
        okText="确认"
        cancelText="取消"
        onCancel={this.handleModalCancel}
        footerRender={handleFooterRender}
      >
        <React.Fragment>
          {content}
        </React.Fragment>
      </Dialog>
    )
  }
}


export default connect()(props => (props.visible ? <UploadDialog {...props} /> : null))
