
import React from 'react'
import { connect } from 'react-redux'
import { NavLink } from 'react-router-dom'

import UploadDialog from '@components/Upload'
import BtnGroup from '@components/BtnGroup'
import Msg from '@components/Msg'
import InputTable from '@components/InputTable'

import Panel from '@layouts/PanelLayout.js'
import DeleteDialog from '@components/DeleteDialog'

class RuleInput extends React.Component {
  constructor(props) {
    super(props)
    this.state = ({
      uplodVisable: false
    })
  }
  componentDidMount() {
    this.fetchData()
  }
  //获取数据
  fetchData = () => {
    this.props.dispatch({
      type: 'glass/fetchRuleTable'
    })
  }
  //新建
  handleCreatBtn = () => {
    this.props.dispatch({
      type: 'glass/change',
      payload: {
        addRulePageShow: true,
        addRulePageColum: this.props.ruleTableColum,
        addRulePageIsEdit: false,
        editColumData: null
      }
    })
  }
  //批量编辑
  handleBulkEdit = () => {
    this.setState({ uplodVisable: true })
  }
  //设置按钮
  handleSettingBtnClick = (setting, record) => {
    if (setting === "edit") {
      this.props.dispatch({
        type: 'glass/change',
        payload: {
          addRulePageShow: true,
          addRulePageColum: this.props.ruleTableColum,
          addRulePageIsEdit: true,
          editColumData: record
        }
      })
    } else {
      let that = this;
      DeleteDialog.open({
        confirmCallBack(startLoading, endLoading) {
          startLoading()
          that.props.dispatch({
            type: "glass/deleteRule",
            payload: {
              id: record.id,
              type: 1
            },
            callback() {
              that.fetchData()
              endLoading()
              DeleteDialog.close()
            },
          })
        },
        cancelCallBack() {
          DeleteDialog.close()
        }
      })
    }
  }
  //分页数改变
  onPerPageChange = (val) => {
    this.props.dispatch({
      type: 'glass/changePage',
      payload: {
        ruleTablePageCount: val
      },
      callback: () => {
        this.fetchData()
      }
    })
  }
  //分页改变
  onPageChange = (val) => {
    this.props.dispatch({
      type: 'glass/changePage',
      payload: {
        ruleTablePageCurrent: val 
      },
      callback: () => {
        this.fetchData()
      }
    })
  }
  render() {
    const { ruleTableColum, ruleTableData, ruleTablePageTotal, ruleTablePageCurrent, ruleTablePageCount } = this.props
    return (
      <React.Fragment>
        <Msg msg='业务说明，当条件部分（黑色字）能够匹配，就更新更新项部分（绿色字）为对应内容；设置完成后请在配置生效界面进行生效操作 '>
          <NavLink exact to="/suggest/apply" activeStyle={{ color: '#4dc060' }}>前往配置生效页面 </NavLink>
        </Msg>
        <BtnGroup
          disabled={false}
          creatFncHandle={this.handleCreatBtn}
          editFncHandle={this.handleBulkEdit}
        >
        </BtnGroup>
        <Panel>
          <InputTable
            ruleTableColum={ruleTableColum}
            ruleTableData={ruleTableData}
            ruleTablePageTotal={ruleTablePageTotal}
            ruleTablePageCurrent={ruleTablePageCurrent}
            ruleTablePageCount={ruleTablePageCount}
            handleSettingBtnClick={this.handleSettingBtnClick}
            onPerPageChange={this.onPerPageChange}
            onPageChange={this.onPageChange}
            loading={this.ruleTableLoading}
          >
          </InputTable>
        </Panel>
        <UploadDialog
          visible={this.state.uplodVisable}
          type={4}
          onCancel={() => {
            this.setState({
              uplodVisable: false
            })
            this.fetchData()
          }}
        ></UploadDialog>
      </React.Fragment>
    )
  }
}

const mapStateToProps = ({ glass }) => {
  return {
    ruleTableColum: glass.ruleTableColum,
    ruleTableData: glass.ruleTableData,
    ruleTablePageTotal: glass.ruleTablePageTotal,
    ruleTablePageCurrent: glass.ruleTablePageCurrent,
    ruleTablePageCount: glass.ruleTablePageCount,
    ruleTableLoading: glass.ruleTableLoading
  }
}

export default connect(mapStateToProps)(RuleInput)
