/**
 * @desc 删除确认框
*/

import Dialog from './Dialog.js';


let dialogInstance = 0;

let getDialogInstance = (options) => {
    dialogInstance = dialogInstance || Dialog.newInstance({
        ...options,
    });
    return dialogInstance;
};
export default {
    open(options = {}) {
        getDialogInstance(options);
    },
    close() {
        if (dialogInstance) {
            dialogInstance.destroy();
            dialogInstance = null;
        }
    },
};
