import React from 'react'
import { connect } from 'react-redux'
import { TabList } from '@tencent/bere'

import RuleDefine from './RuleDefine'
import RuleInput from './RuleInput'
import RulePage from './RulePage'
import Title from '@components/Title'

class App extends React.Component {
  componentDidMount() {
    this.props.dispatch({
      type: 'glass/change',
      payload: {
        addRulePageShow: false
      }
    })

  }
  handleNavSelectChange = (val) => {
    this.props.dispatch({
      type: 'glass/change',
      payload: {
        navSelect: val
      }
    })
  }
  render() {
    const navSelect = this.props.navSelect;
    const navList = [
      {
        value: 'definition',
        text: '规则定义'
      },
      {
        value: 'input',
        text: '规则录入'
      }
    ];
    return (
      <React.Fragment>
        <div className="main adc-manage-main">

          {this.props.addRulePageShow === true ?
            <RulePage></RulePage>
            :
            <div className='manage-area'>
              <Title title='Glass开单建议'></Title>
              <div className="manage-area-main">
                <div className='tc-15-tab tc-15-tab-alt tc-special-tab'>
                  <TabList
                    list={navList}
                    value={navSelect}
                    onChange={this.handleNavSelectChange}
                  />
                  <div className='tab-panel'>
                    <div className='manage-area-main-inner'>
                      {
                        this.props.navSelect === 'definition' ?
                          <RuleDefine></RuleDefine> :
                          <RuleInput></RuleInput>
                      }
                    </div>
                  </div>
                  <div className="pt-10 pb-10 clearfix"></div>
                </div>
              </div>
            </div>
          }
        </div>
      </React.Fragment>
    )
  }
}

const mapStateToProps = ({ glass }) => {
  return {
    addRulePageShow: glass.addRulePageShow,
    navSelect: glass.navSelect,
  }
}

export default connect(mapStateToProps)(App)
