import React from 'react'
import classnames from 'classnames'

const PanelLayout = ({ children, option, title, foot, tip, className, collapse, ...rest }) => <div className={classnames('tc-panel', className)} {...rest}>
  {
    (title || option) && <div className="tc-panel-hd">
      <div className="col">
        <h3 className="title">{title} { tip && <em>{tip}</em> }</h3>
      </div>
      <div className="col">{option}</div>
    </div>
  }
  <div className="tc-panel-bd">{children}</div>
  { foot && <div className="tc-panel-ft">{foot}</div> }
  { collapse && <div className="tc-panel-collapse">{collapse}</div>}
</div>

export default PanelLayout
