import _extends from "@babel/runtime/helpers/extends";
import _objectWithoutProperties from "@babel/runtime/helpers/objectWithoutProperties";
import React from 'react';
import PropTypes from 'prop-types';
import { Field } from 'redux-form';
import { FormGroup } from '@tencent/bere-form';
/**
 * 带 FormGroup 特性的 PureField
 * @param props
 * @returns {*}
 * @constructor
 */

function FormGroupField(props) {
  var component = props.component,
      name = props.name,
      visible = props.visible,
      rest = _objectWithoutProperties(props, ["component", "name", "visible"]);

  if (visible) {
    return React.createElement(Field // noUpdateOnBlur={noUpdateOnBlur}
    , _extends({
      name: name,
      control: component,
      component: FormGroup
    }, rest));
  } else {
    return null;
  }
}

FormGroupField.propTypes = {
  /** 表单域名 */
  name: PropTypes.string.isRequired,

  /** 表单域是否可见 */
  visible: PropTypes.bool,

  /** 表单组件 */
  component: PropTypes.func.isRequired
};
FormGroupField.defaultProps = {
  visible: true
};
export default FormGroupField;